// ----------------
// Functions
// ----------------

function itemLine({ product, quantity }) {
  const { name, price } = product;
  const totalPrice = quantity * price;

  return name + ': ' + quantity + ' * ' + price + 'k = ' + totalPrice +'k\n';
}

function breakLine() {
  return '____________________\n';
}

function sumLine(total) {
  return 'Tng tin hng: ' + total + 'k\n';
}


function buildItemsBlock(items) {
  return items
    .map(itemLine)
    .join('')
}

function computeTotal(items) {
  return items
          .map(item => item.quantity * item.product.price)
          .reduce((acc, curr) => acc + curr, 0)
}

function buildResponse(items) {
  // Items example:
  // [ 
  //    {
  //       product: { name: 'B', price: 10 },
  //       quantity: 10
  //    }
  // ]
  
  // prettier-ignore 
  return buildItemsBlock(items) 
       + breakLine() 
       + sumLine(computeTotal(items))
}

function breakComma(sentence) {
  return sentence.split(',')
}

function breakSentence(sentence) {
  return sentence
            .replace(/\n|vs|vi|voi|va|\+/g, ',')
            .replace(/\d+\s*me/g, '$&,')
            .replace(/\d+\s*mui/g, '$&,')
            .replace(/\d+\s*muoi/g, '$&,')
            .replace(/\d+\s*bo/g, '$&,')
            .replace(/\d+\s*b/g, '$&,')
            .replace(/\d+\s*kh g/g, '$&,')
            .replace(/\d+\s*kho ga/g, '$&,')
            .replace(/\d+\s*ot tac/g, '$&,')
            .replace(/\d+\s*tac/g, '$&,')
            .replace(/\d+\s*tc/g, '$&,')
            .replace(/\d+\s*tt/g, '$&,')
            .replace(/\d+\s*tat/g, '$&,')

}

function matchCondition(line) {
  return function ({regex}) {
    return line ? line.match(regex) : false
  }
}

function detectProduct(line) {
  const testConditions = [
    { regex: /me|bnh trng me|bt me|me/, output: 'bt_me'},
    { regex: /bt mui|bnh trng mui u|bt muoi|muoi dau/, output: 'bt_muoi_dau'},
    { regex: /t tc|t tt|sa t tc|sa te tac|sate t cay|ot cay/, output: 'bt_sa_te_tac'},
    { regex: /trn tc|trn tt|tron tat|bnh trng trn|tron tac|bt tron|tron/, output: 'bt_tron_tac'},
    { regex: /b|bnh b|bnh trng b|banh trang bo|bo/, output: 'bt_bo'},
    { regex: /ti|toi|bnh ti|sate toi|sa t ti|bnh trng sa t ti|bnh trng ti/, output: 'bt_sa_te_toi'},
    { regex: /do cay|deo cay|cay do|cay deo/, output: 'bt_deo_cay'},
    { regex: /do me|deo me|me deo|me do/, output: 'bt_deo_me'},
    { regex: /do tm|deo tom|tom deo|tm do/, output: 'bt_deo_tom'},
    { regex: /hp mui|hop muoi|hu muoi|h mui|hu mui|mui tm|muoi tom/, output: 'muoi_tom'},
    { regex: /mui chay|muoi chay|muoi ot/, output: 'muoi_chay'},
    { regex: /cha bong|ch bng|bt ch bng/, output: 'bt_cha_bong'},
    { regex: /kho ga|kh g/, output: 'kho_ga'},
    { regex: /phi sng|phoi suong|bnh trng phi sng|banh trang ps/, output: 'bt_phoi_suong'},
    { regex: /trn mui t|tron muoi ot/, output: 'bt_tron_muoi_ot'},
    { regex: /chum ruot|chm rut|chum ruoc|chum rut/, output: 'chum_ruot'},
    { regex: /mut me|mt me|mc me|muc me/, output: 'mut_me'},
    { regex: /banh trang vuong|bnh trng vung/, output: 'bt_vuong'},
    { regex: /ng tm|ong tom/, output: 'bt_ong_tom'},

    // Default for duplicated regex
    { regex: /tac|tt|tat|tc/, output: 'bt_sa_te_tac'},
    { regex: /sate|sa t/, output: 'bt_sa_te_tac'},
  ]

  const result = testConditions.find(matchCondition(line.toLowerCase()))

  if (result) {
    return products[result.output]
  }

  return null;  
}

function detectQuantity(line) {
  const matches = line.match(/\d/g);
  return matches != null ? parseInt(matches.join('')) : 0;
}

function detectProductAndQuantity(line) {
  const product = detectProduct(line);
  const quantity = detectQuantity(line);

  return {
    product: product,
    quantity: quantity
  } 
}

function undetectedItem({ product }) {
  return !!product
}

function sentenceToItems(sentence) {
  return breakComma(breakSentence(sentence))
          .map(detectProductAndQuantity)
          .filter(undetectedItem)
}

function extractDescription(input) {
  return buildResponse(sentenceToItems(input))
}

// ----------------
// Main block
// ----------------

module.exports = {
  itemLine,
  breakLine,
  sumLine,
  buildResponse,
  detectQuantity,
  detectProduct,
  sentenceToItems,
  extractDescription
};
